import React, {useEffect} from 'react';
import {RouteComponentProps} from 'react-router-dom';
import Footer from '../components/Footer';
import Header from '../components/Header';
import {useForm} from 'react-hook-form';
import Input from '../components/Input';
import useValidation from '../useHooks/useValidation';

interface IEstimatePageProps extends RouteComponentProps{

}

const EstimatePage = ({location}: IEstimatePageProps) => {
  const {handleSubmit, register, watch, errors} = useForm({
    mode: 'onChange',
    reValidateMode: 'onChange'
  });
  const validation = useValidation();

  const onSubmit = (data: any) => {
    console.log(data);
  };

  useEffect(() => {
    if(location.hash) {
      const el: any = document.querySelector(location.hash);
      if(el) {
        el.scrollIntoView({
          behavior: 'smooth',
          block:  'center',
        });
      }
    }
  }, []);

  return (
    <div className="page-about">
    <div className="page">
     <Header/>

      <main className="estimate-page">
        <div className="container">
          <h1>Estimation</h1>

          <div className="estimate-top">
            <div className="estimate-top_caption">Estimation de la valeur de votre propriété en deux étapes :</div>
            <div className="estimate-top_item one">
              <article className="estimate-top_item-in">
                <span className="number">1</span>
                <span className="icon-sign"></span>
                <p>Entrez vos données</p>
              </article>
            </div>
            <div className="estimate-top_item two">
              <article className="estimate-top_item-in">
                <span className="number">2</span>
                <span className="icon-contact"></span>
                <p>Nous vous contacterons dans les 48 heures avec plus d'informations</p>
              </article>
            </div>
          </div>
        </div>

        <section className="section-estimate">
          <h3>Entrez vos données</h3>

          <form id={'form'} className="estimate-form" onSubmit={handleSubmit(onSubmit)}>
            <div className="row">
              <div className="col-12 col-lg-6">
                <Input errors={errors} name={'fullName'} label={'Nom prénom du demandeur* ' } ref={register(validation.required)} />
                <Input errors={errors} name={'relationOwner'} label={'Lien avec le propriétaire si le demandeur n’est pas propriétaire du terrain (parent, tuteur légal, autre…)* ' } ref={register(validation.required)} />
              </div>
              <div className="col-12 col-lg-6">
                <Input errors={errors} conClassName={'form-item_right'} name={'email'} label={'Adresse email* ' } ref={register(validation.email)} />
                <Input errors={errors} conClassName={'form-item_right'} name={'phone'} label={' Téléphone* ' } ref={register(validation.required)} />
              </div>
              <div className="col-12">
                <p className="form-subtitle">L'un de ces champs est obligatoire *</p>
              </div>
              <div className="col-12 col-lg-6">
                <Input
                  errors={errors}
                  name={'address'}
                  label={'Nom et numéro de rue + commune/code postal' }
                  ref={register(validation.oneOfThemRequired(watch('registryNumber')))}
                />
                <span className="estimate-ou">ou</span>
              </div>
              <div className="col-12 col-lg-6">
                <Input
                  errors={errors}
                  conClassName={'form-item_right'}
                  name={'registryNumber'}
                  label={'Numéro de parcelle + nom de la commune ' }
                  ref={register(validation.oneOfThemRequired(watch('address')))}
                />
              </div>
              <div className="col-12">
                <div className="form-item_checkbox">
                  <input type="checkbox" id="accept" name={'checkbox'} ref={register} />
                    <label htmlFor="accept">J’ai lu et j’accepte la <a href="#">politique de confidentialité</a> de ce
                      site*</label>
                </div>
                <p>
                  <button disabled={!watch('checkbox')} type="submit" className="btn btn-gradient">Envoyer</button>
                </p>
              </div>
            </div>
          </form>

          <div className="estimate-end" style={
            {
              display: 'none'
            }
          }>
            <h3>Demande envoyée</h3>
            <p>Merci d'avoir partagé vos informations.</p>
            Notre équipe vous répondra sous 48 heures.
          </div>
        </section>
      </main>

     <Footer/>
    </div>
    </div>
  )
};

export default EstimatePage;
