import React, {useState} from 'react';
import {Link, useLocation} from 'react-router-dom';
import ContactModal from './ContactModal';

interface IHeaderProps {

}

const routes = [
  {
    path: '/estimate',
    title: 'ESTIMATION'
  },
  {
    path: '/portfolio',
    title: 'PORTFOLIO'
  },
  {
    path: '/services',
    title: 'SERVICES'
  },
  {
    path: '/about',
    title: 'équipe'
  },
];

const Header = ({}: IHeaderProps) => {
  const [isOpen, setIsOpen] = useState(false);

  const l = useLocation();

  return (
    <header className="header">
      <ContactModal isOpen={isOpen} setOpen={setIsOpen}/>
      <div className="container">
        <Link to={'/'} className="header-logo"><img src={require('../assets/images/logo.png')} alt="New Home (logo)" /></Link>
        <nav className="header-nav">
          <ul className="header-menu">
            {
              routes.map((r, i) => {
                const cn = l.pathname.startsWith(r.path) ? 'active' : '';

                return (
                  <li className={cn} ><Link to={r.path} >{r.title}</Link></li>
                )
              })
            }
          </ul>
        </nav>
        <a onClick={() => setIsOpen(true)} className="btn btn-blue btn-left"><span>Contact</span></a>
      </div>
    </header>
  )
};

export default Header;
